.bg-image-swiper {
  height: 733px;
  background-image: url('../images/bg_share.jpg');
  background-position: center center;
  background-size: cover;
}

// swiper-wrapper 
.height-429 {
  height: 429px;
}

// 
.swipe-img {
  img {
    width: 60%;
    height: 381px;
    object-fit: cover;
    border-radius: 24px;

    &~.img-border {
      width: 60%;
      height: 381px;
      position: absolute;
      top: 10px;
      left: -10px;
      z-index: -1;
      border: 1px solid #7C7E77;
      border-radius: 24px;
    }
  }
}

//  swiper  border hidden
.swiper {
  overflow: visible;
}

// 
.swipe-card-text {
  position: absolute;
  bottom: 0;
  right: -24px;
  background-color: #fff;
  width: 416px;
  height: 178px;
  border-radius: 24px 24px 24px 0;

  &-img {
    position: absolute;
    top: -50px;
    right: 32px;
    max-width: 100px;
    border-radius: 50%;
  }
}

// 
.swiper-button-prev::after {
  content: ""; //
  font-size: 0; // icon 
}
.btn-prev-position {
  position: absolute;
  top: 100%;
  left: 196px;
}

// 
.swiper-button-next::after {
  content: ""; //
  font-size: 0; // icon 
}
.btn-next-position {
  position: absolute;
  top: 100%;
  left: 316px;
}

//  
.swiper-pagination-custom {
  position: absolute;
  width: auto !important;
  bottom: 0 !important;
  left: 236px !important;
}
.swiper-pagination-bullet {
  background-color: #62DB54;
  opacity: 1;
}
.swiper-pagination-bullet-active {
  width: 16px;
  border-radius: 8px;
}

// 
.text-green {
  color: #62DB54;
}

// 
.icon-size {
  width: 24px;
  height: 24px;
}